@tailwind base;
@tailwind components;
@tailwind utilities;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

section {
    display: flex;
    height: 100vh;
    width: 100vw;


    .section_bg{
        z-index: -1;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        
    }

    .block_content-description{
        font-size: 2rem;
        text-aligh: center;

    }
    .block_content-btn{
        text-decoration: none;
        font-size: 2rem;
        display: inline-block;
        tarnsform: scale(1);

    }
}

