// ------------------ NAV -----------------------

$darkgrey: rgb(34, 34, 34);

//animation
$transitionall: all 250ms ease-in-out;



.nav_btn-container{
    display: block;
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 110;
    pointer-events: none;

    .nav_btn-grid{
        position: absolute;
        right: 1rem;
        top: 50%;
        //grid-gap: 1rem;
        transform: translateY(-50%)
    }

    .navbtn {
        display: block;
        width: 20px;
        height: 20px;
        background-color: transparent;
        border: 3px solid whitesmoke;
        border-radius: 100%;
        cursor: pointer;
        pointer-events: all;
        position: relative;

        &::after{
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            margin: 3px;
            border-radius: 100%;
            background-color: transparent;
            transition:$transitionall; 
        }
        &:hover::after, &:focus::after {
            background-color: rgba(whitesmoke, .8);
        }
    }
}

nav {
    font-size: 2rem;
    position: fixed;
    z-index: 100;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
    background-color: rgba(whitesmoke, .2);
    .nav-link-container {
        display: flex;
        list-style: none;
    }
    .nav-links{
        text-decoration: none;
        &:link, &:visited {
            color: whitesmoke;
            transition: $transitionall;
        }
        &:active, &:hover, &:focus {

        }

    }
}

